.page_up {
    display: none;
    position: fixed;
    bottom: 40px;
    right: 15px;
    width: 60px;

    &_animate {
        animation-name: arrowtop;
        animation-duration: 1s;
        animation-timing-function: ease;
        animation-iteration-count: infinite;
        animation-play-state: paused;

        &:hover {
            animation-play-state: initial;
        }
    }

    img {
        width: 100%;
    }
}