.profession_section {
    background: url(../img/background/background_profesi.jpg);
    background-size: cover;
    background-repeat: no-repeat;

    height: 100vh;
}

.profession__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    h1 {
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        position: relative;
        top: 60px;
        left: 0;
        color: #00ffff;
        font-size: 100px;
        letter-spacing: 10px;
        padding-top: 70px;
        padding-bottom: 75vh;
    }
}