.portfolio_section {
    padding: 70px 0;
}

.portfolio__wrapper {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.cover {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-around;

    .box_cover {
        padding: 10px 40px;
    }

    .slick-prev {
        background-color: rgba(255, 255, 255, 0);
        border: none;

        img {
            height: 50px;
        }
    }

    .slick-next {
        background-color: rgba(255, 255, 255, 0);
        border: none;

        img {
            height: 50px;
        }
    }
}



.content {
    position: relative;
    padding: 20px;
    color: #ffffff;

    h2 {
        position: absolute;
        top: -60px;
        right: 20px;
        margin: 0;
        padding: 0;
        font-size: 160px;
        line-height: 1.1;
        color: rgba(255,255,255,.06);
        transition: 0.5s;
    }

    h3 {
        margin: 0 0 10px;
        font-size: 24px;
        letter-spacing: 2px;
        line-height: 1.2;
    }

    p {
        margin: 0;
        font-size: 14px;
        letter-spacing: 1px;
        line-height: 1.3;
    }

    button {
        cursor: pointer;
        position: relative;
        margin: 20px 0 0;
        padding: 10px 0;
        background-color: transparent;
        border: 1px solid #ffffff;
        color: #ffffff;
        transition: 0.5s;
        transform: translateY(-40px);
        opacity: 0;
        visibility: hidden;
        text-transform: uppercase;
        font-size: 12px;
        z-index: 2;

            a {
                text-decoration: none;
                padding: 10px 10px;
                transition: 0.5s;

                &:hover {
                    color: #000000;
                    background-color: #fff;
            }
        }
    }
}

.box {
    width: 260px;
    min-height: 385px;
    cursor: pointer;
    position: relative;
    background-color: #040a20;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    border: 1px solid rgba(0,0,0,.5);

    &:hover {
        .content {
            h2 {
                top: -125px;
            }

            button {
                transform: translate(0);
                opacity: 1;
                visibility: visible;
            }
        }
    }

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 50%;
        height: 100%;
        background-color: rgba(255,255,255,.06);
    }

    &::before {
        content: '';
        position: absolute;
        top: -3px;
        left: -3px;
        right: -3px;
        bottom: -3px;
        background-color: #fff;
        transform: skew(2deg,2deg);
        z-index: -1;
    }

    &:nth-child(1) {
        &:before {
            background: linear-gradient(#ff0000, #0037ff);
        }
    }

    &:nth-child(2) {
        &:before {
            background: linear-gradient(#d8ff00, #17f4ff);
        }
    }

    &:nth-child(3) {
        &:before {
            background: linear-gradient(#af36ff, #ff0070);
        }
    }
}