.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    transition: 1s all;
    opacity: 1;
    visibility: visible;

    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(#000, #01042d);


    .loader {
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        position: relative;
        color: #212121;
        font-size: 150px;
        letter-spacing: 10px;
        text-transform: uppercase;

        &::before {
            position: absolute;
            top: 0;
            left: 0;
            content: 'Loading...';
            color: #00ffff;
            overflow: hidden;
            animation: loading 5s infinite linear;
        }
    }

    &.done {
        opacity: 0;
        visibility: hidden;
    }
}

@keyframes loading {
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}