header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.01);
    z-index: 1;
}

.header_fixed {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;
    background: linear-gradient(#01042d, #000);
    box-shadow: 0 10px 10px 0 rgba(1, 4, 45, 0.5);
    animation: showElement;
    animation-duration: 1s;
}

@keyframes showElement {
    0% {
        opacity: 0;
        top: -200px;
    }

    100% {
        opacity: 1;
        top: 0;
    }
}

.header_wrapper {
    min-height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    h2 {
        font-size: 30px;
    }

    .switcher {
        position: absolute;
        visibility: hidden;
        left: -9999px;
    }

    label {
        display: none;
        cursor: pointer;
        height: 30px;

        img {
            height: 100%;
        }
    }

    ul {
        list-style-type: none;
        display: flex;
    }

    a {
        display: block;
        text-transform: uppercase;
        text-decoration: none;
        padding: 0 15px;
        transition: 0.4s esse;

        &:hover {
            color: #00eeff;
            text-decoration: underline;
            transition: 0.4s ease-in;
        }
    }
}

@media screen and (max-width: 900px) {

    .header_wrapper {

        .open,
        .close {
            display: block;
        }

        .open {
            padding: 0 20px 0 0;
        }

        .close {
            position: absolute;
            top: 40px;
            right: 40px;
        }

        .switcher:checked ~ nav {
            right: 0;
            z-index: 1;
        }

        nav {
            position: fixed;
            width: 100%;
            height: 100vh;
            background: linear-gradient(#000, #01042d);
            top: 0;
            right: -100%;
            text-align: center;
            padding: 100px 0;
            transition: 0.5s;

            ul {
                flex-direction: column;

                a {
                    padding: 20px;

                    &:hover {
                        background: linear-gradient(90deg, rgba(0, 0, 0, 0), rgba(0, 238, 255, 0.15), rgba(0, 0, 0, 0));
                    }
                }
            }
        }
    }
}